.carousel-container {
    width:100%;
    display: flex;
    flex-direction: row;
}

.carousel-wrapper {
    display:flex;
    width:100%;
    position:relative;
}

.carousel-content-wrapper {
    overflow: hidden;
    width:100%;
    height:100%;
}

.carousel-content {
    display:flex;
    padding-top:20px;
    transition:all 250ms linear;
}

.carousel-content > * {
    width: 100%;
    text-align: center;
    flex-shrink: 0;
    flex-grow: 1;
}

.left-arrow,
.right-arrow {
    position:absolute;
    z-index: 1;
    top: 50%;
    transform: translateY(-50%);
    border-radius: 24px;
    border: 1px solid #ddd;
}

.left-arrow {
    left:24px;
}

.right-arrow {
    right:24px;
}

@media (max-width: 575px) {
    img {
        width: 275px;
        height: 183px;
    }

    .left-arrow, .right-arrow {
        display: none;
    }
}