* {
    box-sizing: border-box;
}

.mobile-nav {
    display: none;
}

.hamburger-menu {
    width: 17px;
    height: 22px;
    margin-right:10px;
    margin-bottom:15px;
}

.main-logo {
    width:74px;
    height:17px;
    margin-bottom:20px;
}

.navbar {
    height:50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 15px;
    font-weight: 600;
      
}

.line {
    box-shadow: 0 1px 0 0 rgb(0 0 0 / 10%); 
}

ul,
li {
    list-style-type: none;
    padding-left: 0;
    margin-left:0 ;
    display: inline-block;
    padding-right: 15px;
}

li {
    float: left;
}

.newbadge {
    display: inline-flex;
    width: 12px;
    height: 12px;
    border-radius:35%;
}

.notidiv {
    display: flex;
}

.profilediv {
    display: flex;
    position: relative;
}

.notidiv {
    display: flex;
    position: relative;
}

.badgediv {
    position: absolute;
    top:-12px;
    left:23px;
}

.notibadgediv {
    position: absolute;
    top:-12px;
    left:15px;
}

.avatar {
    width: 32px;
    height: 32px;
    display: flex;
    border-radius: 50%;
    border: 1px solid #e1e2e3;
    background-color: #fff;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.profile {
    width:28px;
    height: 28px;
    display: block;
    margin: 0;
    border-radius: 50%;
    background: url('../assets/profile.png');
    background-position: 50%;
    background-size: cover;
    background-repeat: no-repeat;
}

.enter {
    display: none;
}

.co-service:before {
    display: block;
    content: "";
    width: 1px;
    height: 10px;
    background-color: #e1e2e3;
    margin: 10px 10px;
}

.co-service {
    display: inline-flex;
    position: relative;
    vertical-align: middle;
}

.co-service p {
    display: inline-block;
    font-size: 13px;
    color: #666;
    line-height: 30px;
    height: 30px;
    border: 1px solid #e1e2e3;
    border-radius: 15px;
    padding: 0 10px;
    margin-left: 15px;
    font-weight: 400;
}

.new {
    margin-top:3px;
    margin-left:3px;
}

@media (max-width: 575px) {
    .first-ul,
    .second-ul {
        display: none;
    }

    .enter {
        display: inline-flex;
        position: relative;
        margin-left: 140px; 
    }
    
    .enter p {
        display: inline-block;
        font-size: 13px;
        color: blue;
        line-height: 30px;
        height: 30px;
        border: 1px solid blue;
        border-radius: 15px;
        padding: 0 10px;
        margin-left: 15px;
        font-weight: 400;
    }

    .ham-logo-enter {
        display: flex;
        justify-content: space-around;
    }

    .mobile-nav {
        display: flex;
        justify-content: space-between;
    }

    .mobile-first {
        margin-left:15px;
    }

    .mobile-second {
        margin-right: -20px;
    }
}


